import * as React from "react"

var products = [
  { name: "Gameboy 200", price: 10000 },
  { name: "Samsung A3", price: 20000 },
  { name: "Airpods Pro", price: 9000 }
]

function AboutPage() {
    let total = 0;

    for(let p in products) {
        total += products[p].price
      }


  return (
    <main>
      <h1>About Page</h1>
      <div>
        <table border="1">
          <thead>
            <tr>
              <th>Name</th><th>Price</th>
            </tr>
          </thead>
          <tbody>
            {
              products.map(p => (
                <tr><td>{p.name}</td><td>{p.price}</td></tr>
              ))
            }
          </tbody>
          <tfoot>
            <tr>
              <td>Total</td>
              <td>{total}</td>
            </tr>
          </tfoot>
        </table>
      </div>
    </main>
  )
}

export default AboutPage